@tailwind base;
@tailwind components;
@tailwind utilities;

.btn{
    background: linear-gradient(
        92.23deg,
        #ff56f6 21.43%,
        #b936ee 50.63%,
        #3bace2 100%,
        #406aff 117.04%
    );
    box-shadow: 0px 4.42184px 107.23px rgba(255,86,246,0.51);
}
.btn hover {
    background: linear-Gradient(
        92.23deg,
        #406aff 21.43%,
        #3bace2 50.63%,
        #b936ee 100%,
        #ff56f6 117.04%
    );
}
.gradient,

.active {
    background : linear-gradient(
        9292.23deg,
        #406aff 21.43%,
        #3bace2 50.63%,
        #b936ee 100%,
        #ff56f6 117.04%
    );
}
.active {
    color : #FFF;
    padding : 1rem;
    border-radius: 100%;
}
@layer base {
    body {
        @apply font-secondary text-lg leading-8 text-white;
    }
    .h2{
        @apply font-primary text-[32PX] mb-6 tracking-[10%] uppercase;
    }
    .h3{
        @apply font-primary font-semibold text-[26px] mb-6 leading-[46px];
    }
    .btn{
        @apply rounded-full font-primary text-white font-medium;
    }
    .btn-sm{
        @apply h-[48px] px-6 text-sm;
    }
    .btn-lg{
        @apply h-[56px] px-10 text-base;
    }
    .btn-link {
        @apply text-base;
    }
    .text-gradient{
        @apply bg-gradient-to-r text-transparent bg-clip-text from-[#42A6E3] to-[#FF56f6] font-bold font-primary hover:from-[#ff5656]
        ;
    }
    .section {
        @apply py-8 lg:h-screen flex items-center;
    }
}